<template>
  <div class="tabbaritemWrap" :class="{active:!bol}" @click="clickFn">
    <span v-show="bol"><slot name="normalImg"></slot><br/></span>
    <span v-show="!bol"><slot name="activeImg"></slot><br/></span>
    <span>{{ txt }}</span>
  </div>
</template>

<script>
export default {
  props: ["txt","mark","selecte"],
  methods: {
    clickFn: function () {
      console.log(this.mark);
      this.$emit("change",this.mark);
      this.$router.push("/"+ this.mark);
    }
  },
  computed: {
    bol: function () {
      if (this.mark==this.selecte){
        return false;
      }
      return true;
    }
  }
}
</script>

<style scoped>
  .tabbaritemWrap {background: #2F2F2F;height: .55rem;width: 25%;float: left;font-size: 0;text-align: center;font-size: .12rem;}
  .tabbaritemWrap img {width: .4rem;}
  .tabbaritemWrap span {color: #fff;}
  .tabbaritemWrap.active {background: #ED3037;}
</style>



