<template>
  <div class="tabbarWrap clearfix">
    <Tabbaritem txt="" mark="home" :selecte="selected" @change="getVal">
      <img src="../../assets/images/i-home.png" alt="" slot="normalImg"/>
      <img src="../../assets/images/i-home-active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="category" :selecte="selected" @change="getVal">
      <img src="../../assets/images/i-category.png" alt="" slot="normalImg"/>
      <img src="../../assets/images/i-category-active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="shopping" :selecte="selected" @change="getVal">
      <img src="../../assets/images/i-cart.png" alt="" slot="normalImg"/>
      <img src="../../assets/images/i-cart-active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
    <Tabbaritem txt="" mark="mine" :selecte="selected" @change="getVal">
      <img src="../../assets/images/i-wo.png" alt="" slot="normalImg"/>
      <img src="../../assets/images/i-wo-active.png" alt="" slot="activeImg"/>
    </Tabbaritem>
  </div>
</template>

<script>
  import Tabbaritem from '../../components/tabbaritem'
  export default {
    components: {
      Tabbaritem
    },
    data: function () {
      return {
        selected: "home"
      }
    },
    methods: {
      getVal: function (val) {
        console.log(val);
        this.selected=val;
      }
    }
  }
</script>

<style scoped>
  .tabbarWrap {width: 100%;position: fixed;left: 0;bottom: 0;}
</style>



