import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/pages/home/home'
import Category from '@/pages/category/category'
import Mine from '@/pages/mine/mine'
import Search from '@/pages/search/search'
import Shopping from '@/pages/shopping/shopping'
import Details from '@/pages/details/details'

//
import Index from '@/pages/home/index'
import Special from '@/pages/home/special'
import Beauty from '@/pages/home/beauty'
import Fruits from '@/pages/home/fruits'

//category 
import Wntj from '../pages/category/wntj'
import Tmzc from '../pages/category/tmzc'
import Mrxp from '../pages/category/mrxp'
import Mzrm from '../pages/category/mzrm'
import Gfhz from '../pages/category/gfhz'
import Jpms from '../pages/category/jpms'
import Dbtc from '../pages/category/dbtc'
import Slxg from '../pages/category/slxg'
import Lxfs from '../pages/category/lxfs'
import Nvxx from '../pages/category/nvxx'
import Ssxb from '../pages/category/ssxb'
import Yybj from '../pages/category/yybj'
import Myyp from '../pages/category/myyp'
import Mjmc from '../pages/category/mjmc'
import Jjry from '../pages/category/jjry'
import Smjd from '../pages/category/smjd'
import Sssp from '../pages/category/sssp'
import Ppzc from '../pages/category/ppzc'

import Mine10 from '@/pages/mine/mine10'
import Mine9 from '@/pages/mine/mine9'
import Lmine from '@/pages/mine/L_mine'

import Tabbar from '../pages/home/tabbar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      components: {
        default: Home,
        Tabbar
      }
    },
    {
      path: '/home',
      name: 'Home',
      components: {
        default: Home,
        Tabbar
      },
      children: [
        {path: '', component: Index},
        {path: 'index', component: Index},
        {path: 'special', component: Special},
        {path: 'beauty', component: Beauty},
        {path: 'fruits', component: Fruits}
      ]
    },
    {
      path: '/category',
      name: 'Category',
      components: {
        default: Category,
        Tabbar
      },
      children:[
      	{path:'',component: Wntj},
      	{path:'wntj',component: Wntj},
      	{path:'tmzc',component: Tmzc},
      	{path:'mrxp',component: Mrxp},
      	{path:'mzrm',component: Mzrm},
      	{path:'gfhz',component: Gfhz},
      	{path:'jpms',component: Jpms},
      	{path:'dbtc',component: Dbtc},
      	{path:'slxg',component: Slxg},
      	{path:'lxfs',component: Lxfs},
      	{path:'nvxx',component: Nvxx},
      	{path:'ssxb',component: Ssxb},
      	{path:'yybj',component: Yybj},
      	{path:'myyp',component: Myyp},
      	{path:'mjmc',component: Mjmc},
      	{path:'jjry',component: Jjry},
      	{path:'smjd',component: Smjd},
      	{path:'sssp',component: Sssp},
      	{path:'ppzc',component: Ppzc}
      ]
    },
    {
      path: '/mine',
      name: 'Mine',
      components: {
        default: Mine,
        Tabbar
      }
    },
    {
      path: '/search',
      name: 'Search',
      components: {
        default: Search
      }
    },
    {
      path: '/shopping',
      name: 'Shopping',
      components: {
        default: Shopping,
        Tabbar
      }
    },
    {
      path: '/details/:id',
      name: 'Details',
      components: {
        default: Details,
        Tabbar
      }
    },
    {
      path:'/mine10',
      name:'Mine10',
      component:Mine10
    },
    {
      path:'/L_mine',
      name:'Lmine',
      component:Lmine
    },
    {
      path:'/mine9',
      name:'Mine9',
      component:Mine9
    }
  ]
})



