		  console.log(this.$store.state.temporaryData[0].id)
      let id = this.$store.state.temporaryData[0].id
      let temp = [];
		  temp = this.$store.state.commodityList;
		  temp.forEach((ele)=>{
        if(id == ele.id){
          this.info = ele;
        }
      })
    }
	}
</script>
<style type="text/css" scoped>

.detailstou .swiper-container{ height: 2.5rem;  }
.detailstou .swiper-container img{width: 100%; }
