<template>
	<div class="productWrap" @click="fn()">
		<p>{{ txt }}</p>
		<slot name="shoping"></slot>
	</div>
</template>

<script>
	export default {
 		props: ['txt'],
 		methods: {
	      fn(){
          this.$store.state.temporaryData.push({id: this.txt})
	        this.$router.push({name: "Details", params: {id: this.txt}})
	      }
	    }
	}
</script>

<style>
	.productWrap{width: 30%;background: #FFFFFF;text-align: center;font-size: .12rem;margin-bottom: .04rem;float: left;margin-left: .05rem;}
	.productWrap img{width: 90%;}
</style>



