<template>

	<div class="z_head">
			<a href="javascript:;" @click="goback()"><img src="../assets/images/back.png" ></a>
			<slot name='shopping'></slot>
			<slot name='edit'></slot>
		</div>

	</div>
</template>
<script type="text/javascript">
	export default {
		props:['htxt','ptxt'],
    methods: {
      goback(){
        this.$router.go(-1)
      }
    }

	}

</script>
<style type="text/css" scoped>
.z_head {height: .44rem; background: #2F2F2F;font-size:.16rem; color: #fff; line-height: .44rem; text-align: center;position: fixed; top: 0; left:0; width: 100%; z-index: 10;}
.z_head span{margin-left: .4rem;}
.z_head p{float: right; padding-right: .15rem;}
.z_head a img{width: .15rem; height: .20rem; position: absolute; top: .12rem; left: .15rem;}
</style>



