<template>
  <div class="specialitemWrap">
    <a href="javascript:;">
      <slot name="pic"></slot>
      <p>{{ ptxt }}</p>
      <span>{{ price }}<i>{{ oldprice }}</i></span>
    </a>
  </div>
</template>

<script>
  export default {
    props: ["ptxt","price","oldprice"]
  }
</script>

<style scoped>
  .specialitemWrap {text-align: center;font-size: 0;}
  .specialitemWrap a {display: block;font-size: .12rem;}
  .specialitemWrap p {color: #272727;}
  .specialitemWrap span {color: #E90C1D;}
  .specialitemWrap span i {color: #272727;text-decoration: line-through;}
  .specialitemWrap img {width: .87rem;display: block;}
</style>



