<template>
  <div class="topnavWrap">
    <a href="javascript:;" @click="jumpFn()"></a>
  </div>
</template>

<script>
  import L_mine from '../mine/L_mine'
  export default {
    components: {
      L_mine
    },
    methods: {
      jumpFn: function (){
        this.$router.push({path: "/category"});
      }
    }
  }
</script>

<style scoped>
  .topnavWrap {background: #fff;}
  .topnavWrap a {height: .3rem;line-height: .3rem;font-size: .13rem;display: block;width: 2.83rem;border-radius: .05rem;padding: 0 .36rem;color: #ccc;margin: .1rem;background: #F7F7F7 url(../../assets/images/home-searchbar-icon.png) no-repeat .1rem center;background-size: .2rem .2rem;}
</style>



