<template>
  <div class="shoppingWrap">
  	<zzw_head>
		<span slot='shopping'></span>
		<p slot='edit' @click='fn2'>{{txt}}</p>
  	</zzw_head>
	<div class="shopphead" v-show='isshow'>
		<zcheckall :isbol="thisblo" @setbols='setbol'>
			<img src="../../assets/images/kongbai.png" slot=nomal >
			<img src="../../assets/images/xuanzhong.png" slot=active>
		</zcheckall>
		<div class="shoppbar">
			<Shopping_bar  @getbols="getblo" :allcheck='thisblo' @guangbais='guangbai'></Shopping_bar>
		</div>
	</div>

	<div class="guangguang" v-show='!isshow'>
		<div class="guangcont">
			<img src="../../assets/images/kong_cart.png" />
			<div class="textguang">
				<p>T.T</p>
				<p class="smalltxt"></p>
        <router-link to="/home/index">
          <button type="button"></button>
        </router-link>

			</div>

		</div>
	</div>

  </div>
</template>
<script type="text/javascript">
	import zzw_head from '../../components/zzw_head.vue'
	import zcheckall from "./z_checkall.vue"
	import Shopping_bar from '../../components/Shopping_bar.vue'
	export default{
		data:function(){
			return{
				thisblo:false,
				txt:'',
				isshow:true
			}
		},

		methods:{
			getblo:function(val){
				this.thisblo=val;

			},
			setbol:function(val){
				this.thisblo=val;

			},
			guangbai:function(val){
				this.isshow = val;
				 console.log(val)
			},
			fn2:function(){

				var div = document.getElementsByClassName("shoppbar")[0];

				if(this.txt==''){
					this.txt='';
					div.style.paddingRight= '0rem';
					console.log(1)

				}else{
					div.style.paddingRight= '.6rem';
					this.txt='';
					console.log(2)
				}


			}

		},
		components:{
			zzw_head,
			zcheckall,
			Shopping_bar
		}
	}
</script>
<style type="text/css" scope>
.shopphead{margin-top: .44rem; }
.guangguang {font-size: .12rem; text-align: center; background: #EEEEEE; width:100%; height: 100%; padding-bottom: 3.8rem;}
.guangcont img{padding-top: .55rem; width: 1.2rem;height: 1.2rem;}
.guangcont p{font-size: .14rem; color: #000; }
.guangcont .smalltxt{font-size: .12rem; color: #969696; line-height: .3rem;}
.textguang {padding-top: .2rem;}
.textguang button{ border:none; width: 1.15rem; height: .3rem; background: #FDE3E3; border:1px solid #ED3036; color: #ED3036; border-radius: 0.05rem;  margin-top: .1rem;}

</style>






