<template>
	<div class="zchackall">

	<div  @click='fn1'>
		<span v-show='!mybol'><slot name='nomal' ></slot></span>
		<span v-show='mybol'><slot name='active'></slot></span>
	</div>
	
		<span class="zcheckall_r"></span>
	</div>
</template>
<script type="text/javascript">
	export default {
		data:function(){
			return {
				mybol:this.isbol
			}
		},
		props:["isbol"],
		watch:{
			isbol:function(){
				this.mybol=this.isbol;
			}
		},

		methods:{
			fn1:function(){
				 this.mybol=!this.mybol;
				 this.$emit('setbols',this.mybol)	
			}
		}
	}
</script>
<style type="text/css" scoped>
.zchackall {position: relative; height: .4rem; line-height: .4rem; font-size: .12rem; }
.zchackall img{position: absolute; width: .2rem; height: .2rem; left: .1rem; top:.11rem; }
.zchackall .zcheckall_r{padding-left: .38rem; font-size: .14rem; color: #000;}
</style>


