<template>
  <div class="clickviewWrap clearfix" :class="{small:bol}"  @click="change()">
    <a href="javascript:;" class="left clearfix"><slot name="top"></slot></a>
    <div class="right">
      <p>{{ ptxt}}</p>
      <span>{{ stxt }}</span>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["ptxt","stxt","bol"],
    methods: {
      change(){
        //idid
        this.$store.state.temporaryData.push({id: this.ptxt})
        this.$router.push({name: "Details", params: {id: this.ptxt}})
      }
    }
  }
</script>

<style scoped>
  .clickviewWrap {background: #F0F0F0;margin-bottom: .05rem;margin: 0 .1rem;}
  .clickviewWrap img {float: left;width: 100%;}
  .clickviewWrap .left {padding-left: 10%;display: block;float: left;width: 30%}
  .clickviewWrap .right {float: left;width: 60%;font-size: .16rem;text-align: right;padding-top: .05rem;}
  .clickviewWrap .right p {color: #696969;font-weight: bold;}
  .clickviewWrap .right span {color: #7D7D7D;}
  .clickviewWrap.small .right {text-align: left;}
  .clickviewWrap.small .right span {background: red;color: #fff;padding: .02rem;}
  .clickviewWrap.small .right p {color: #323232;}
</style>



