<template>
	<div>
		<Titles txt=""></Titles>
		<Product :txt="item.id" v-for="item in info">
			<img src="../../assets/images/22.png" slot="shoping"/>
		</Product>
	</div>
</template>

<script>
	import Titles from '../../components/c_title'
	import Product from '../../components/c_product'
	export default{
		components:{
			Titles,
			Product
		},
    data(){
		  return {
		    info: []
      }
    },
    created(){
		  this.info = this.$store.state.commodityList
    }
	}
</script>

