<template>
  <div class="homeWrap">
    <Topnav></Topnav>
    <ul class="navigation clearfix">
      <li :class="{active: nowIndex==1}" @click="nowIndex=1">
        <router-link to="/home/index"></router-link>
      </li>
      <li :class="{active: nowIndex==2}" @click="nowIndex=2">
        <router-link to="/home/special"></router-link>
      </li>
      <li :class="{active: nowIndex==3}" @click="nowIndex=3">
        <router-link to="/home/beauty"></router-link>
      </li>
      <li :class="{active: nowIndex==4}" @click="nowIndex=4">
        <router-link to="/home/fruits"></router-link>
      </li>
    </ul>
    <div class="stagenavcontent">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import Topnav from './topnav'
  import Carousel from '../../components/carousel'
  export default {
    components: {
      Topnav,
      Carousel
    },
    data: function () {
      return {
        nowIndex: 1
      }
    },
    watch:{//
      $route(to){
        //,nowIndex
        if(to.path=="/home"){
          this.nowIndex=1;
        }
       console.log(to);
      }
    }
  }
</script>

<style scoped>
  .navigation {border-bottom: 1px solid #e2e2e2;}
  .navigation li {width: 25%;height: .31rem;font-size: .13rem;float: left;text-align: center;border-bottom: 1px solid transparent;}
  li a {color: #333;}
  .navigation .active {border-color: #ed4848;}
  .navigation .active a {color: #ed4848;}
</style>



