<template>
	<div class="box">
		<div @click="goback()"><slot name="letfImg" class="letfImg"></slot></div>
		<h3>{{ text }}</h3>
		<slot name="rightImg" class="rightImg"></slot>
	</div>
</template>
<script>
	export default {
		props:['text'],
    methods: {
      goback(){
        this.$router.go(-1)
      }
    }
	}
</script>
<style>
	.box{widows: 100%;background: #262626;position: relative;height: 0.5rem;}
	.box .letfImg{float: left;transform: rotate(180deg);width: .11rem;position: absolute;left: 0.12rem;top: 0.12rem;}
	.box .rightImg{float: right;width: 0.53rem;position: absolute;right: 0;top: .04rem;}
	.box h3{text-align: center;color: #fff;font-size: 0.15rem;font-weight: normal;line-height: 0.5rem;}
</style>



