Vue.config.productionTip = false

/* eslint-disable no-new */
//store
const vuex_store = new Vuex.Store({
  state: {
    commodityList: [
      {'url':require('./assets/images/22.png'),'htxt':'6298Apple/7 inhone 7 plus 128G 4G','ptxt':'1800g/','price':'218',bol:false, id: "1",num:0},
      {'url':require('./assets/images/aa.png'),'htxt':'Aptamil2800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false, id: "2",num:0},
      {'url':require('./assets/images/22.png'),'htxt':'Aptamil3800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false, id: "3",num:0},
      {'url':require('./assets/images/aa.png'),'htxt':'Aptamil4800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false, id: "4",num:0}
    ],
    temporaryData: []
  }
})

new Vue({
  el: '#app',
  store: vuex_store,//store
  router,
  template: '<App/>',
  components: { App }
})



