<template>	
	<div class="detailswarp">
		<p>{{htxt}}</p>
		<p class="price_xianshi"><span class="price">{{price}}</span>|<span class="xianshi">{{xianshi}}</span></p>
		<div class="classify">
			<span>{{span1}}</span>
			<span>{{span2}}</span>
			<span>{{span3}}</span>
		</div>		
	</div>

</template>
<script type="text/javascript">
	export default{
		props:['htxt','price','xianshi','span1','span2','span3']
	}
</script>
<style type="text/css" scope>
 .detailswarp p{font-size: .18rem; padding-right: .2rem; }

.price_xianshi .price {font-size: .15rem; color: #D92E2E;}
.price_xianshi .xianshi{font-size: .05rem; background: #D92E2E; color: #fff; padding:.03rem;}
.classify {font-size: .15rem; padding-top: .2rem;}
.classify span{border:1px solid #D1D0D0; padding: .04rem .14rem; margin-right: .1rem; border-radius: .03rem; color: #666;}	
</style>


