<template>
	<div class="shopp_barwrap" >
		<div v-for='(item,index) in info' class="shoppmarp">
			<div class="shopp_barcont " >
				<div class="shopp_bar_head" >
					<span v-show='!item.bol'><img src='../assets/images/kongbai.png' alt=""  @click='fn1(index)' ></span>
					<span v-show='item.bol'><img src="../assets/images/xuanzhong.png" alt=""  @click='fn1(index)' ></span>
				</div>
				<div class="shopp_barcontimg">
					<span><img :src="item.url" alt=""></span>
				</div>
				<div class="shopp_barcontcont">
					<p class="shopp_barcont_h">{{item.htxt}}</p>
					<p class="shopp_barcont_t">{{item.ptxt}}</p>
				</div>

				<div class="shopp_price">
					<p>{{item.price}}</p>
					<div class="shopp_price_right">
						<button type="button" @click='jian(index)'>-</button>
						<input type="text" :value="item.num" disabled="nomal" />
						<button type="button" @click='jia(index)'>+</button>
					</div>
				</div>
			</div>
			<div class="delete" @click='deletej(index)'>
				
			</div>

		</div>
			<div class="Total">
				<p class="Total_left"> :  <span>{{   price() }}</span></p>
				<p class="Total_right">(<span>{{ numall() }}</span>)</p>
			</div>
	</div>
</template>
<style type="text/css" scoped >

.shoppmarp{position: relative; margin-top: .2rem; }
.shopp_barwrap { width: 100%;border-top: 1px solid #eee; font-size: .12rem; padding-bottom: 1.5rem; }
.shopp_barwrap .shopp_bar_head img{ width: .2rem; height: .2rem; position: absolute;  left: .09rem; top:.48rem; }
.shopp_barwrap .shopp_bar_head span{display: block; width: .38rem;}
.shopp_barwrap .shopp_barcont { padding:.1rem 0 .15rem .38rem; position: relative;}
.shopp_barwrap .shopp_barcont .shopp_barcontimg{width: 30%; height: .9rem; float: left; display: block;}
.shopp_barwrap .shopp_barcont  .shopp_barcontimg img{width: 100%; height: .9rem; }
.shopp_barwrap .shopp_barcont .shopp_barcontcont{float: left; width: 70%;}
.shopp_barwrap .shopp_barcont .shopp_barcontcont p {padding-left:0.07rem; }
.shopp_barcont .shopp_barcont_h {color: #000;}
.shopp_barcont_t{color: #D6D0DB; padding-top: .15rem;}
.shopp_barwrap .shopp_price_right{float: right; }
.shopp_barwrap .shopp_price_right button {width: .24rem; height: .24rem;border:1px solid #DFDFDF; background: #fff;}
.shopp_barwrap .shopp_price_right input{width: .24rem; height: .24rem; border:1px solid #DFDFDF; text-align: center; border:nomal; background: #fff;}
.shopp_barwrap .shopp_price{padding-left: 1.1rem; }
.shopp_barwrap .shopp_price p{font-size: .15rem; color: red; }
.shopp_barwrap .shopp_price .shopp_price_right {margin-top: -0.15rem;}
.shopp_barwrap .delete{width: .6rem; height: 1.15rem; background: #ff3b30; text-align: center; line-height: 1.15rem;position: absolute; top: 1px; right:-.6rem; color: #fff;}
.shopp_barwrap .Total{border-top: 1px solid #eee; height: .4rem; line-height: .4rem; font-size: .15rem; margin-top: .2rem;}
.shopp_barwrap .Total_left{float: left; padding-left: .2rem;}
.shopp_barwrap .Total_right{float: right;  background: #FF3B30; color: #fff; padding: 0 .1rem;}
.shopp_barwrap .Total_left span{ color: #ED3037; }
</style>

<script type="text/javascript">
	export default {

	data:function(){
		return {

			num:0,
//			arr:[
//					{'url':require('../assets/images/22.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0},
//					{'url':require('../assets/images/aa.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0},
//					{'url':require('../assets/images/22.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0},
//					// {'url':require('../assets/images/aa.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0},
//					// {'url':require('../assets/images/aa.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0},
//					// {'url':require('../assets/images/aa.png'),'htxt':'Aptamil1800g/-1800g/','ptxt':'1800g/','price':'45.00',bol:false,num:0}
//
//				],
      info: []
		}
	},
    props:['allcheck'],
	methods:{
		fn1:function(i){
			var _this= this;


			console.log(this.info[i].num)
			this.num = 0;
			this.info[i].bol=!this.info[i].bol;
			if(this.info[i].bol){
				this.info[i].num=1;
			}else{
				this.info[i].num=0;
			}
			this.info.forEach(function(item){
				if(item.bol){
					_this.num++;
					if(_this.num>=_this.info.length){
						_this.$emit('getbols',true);
					}else{
						_this.$emit('getbols',false);
					}

				}else{
					item.num=0;
				}

			})


		},
		deletej:function(i){
			var arr1=[];
			arr1 = this.info;
			this.info=[];
			arr1.splice(i,1);
			for(var i=0;i<arr1.length;i++){
				this.info.push(arr1[i]);


			}

				if(this.info.length<1){
					this.$emit('guangbais',false);
				}else{
					this.$emit('guangbais',true);
				}
		},
		numall:function(){
			var allnum=0;
			this.info.forEach(function(item){
				allnum += parseInt(item.num);
			})

			return allnum;
			console.log(this.info[0].num)
		},
		price:function(){
			var allprice=0;
			this.info.forEach(function(item){
				allprice += parseInt(item.num*item.price);
			})
			return allprice;
		},
		jian:function(i){
			if(this.info[i].bol){
				this.info[i].num--;
				if(this.info[i].num<0){
					this.info[i].num=0;
				}
			}

		},
		jia:function(i){
			if(this.info[i].bol){
				this.info[i].num++;
				if(this.info[i].num>99){
					this.info[i].num=99;
				}
			}

		}
	},
	watch:{
		allcheck:function(){
			var _this=this;

			this.bol=this.allcheck;
			if(this.bol){

			this.info.forEach(function(item,i){
	 		item.bol= _this.bol;
	 		_this.num=4;
	 		item.num= 1;
	 	})
	 }
	 	else{
	 		if(_this.num==0 || _this.num==4){
		this.info.forEach(function(item,i){
	 		item.bol= _this.bol;
	 		item.num= 0;
	 		})

	 	}
	 }



	}
},
	// mounted:function(){
	// 	var _this =this;
	// 	this.arr.forEach(function(value,i){
	// 		_this.arr[i].bol= _this.allcheck;
	// 	})
	// }
    created(){
      let id = this.$store.state.temporaryData[0].id;
      let temp = [];
      temp = this.$store.state.commodityList;
      temp.forEach((ele) =>{
        if(id == ele.id){
          ele.num = 1;
          ele.bol = true;
        this.info.push(ele);
        console.log(ele)
      }
    })
    }
}
</script>



